import Link from "next/link";

const topics = [
  {
    id: "database",
    title: "Database Management",
    description: "Data models, SQL, normalization",
    chapters: [
      "Introduction to Databases",
      "Data Models",
      "SQL Basics",
      "Advanced Queries",
      "Normalization",
      "Exam Practice",
    ],
  },
  {
    id: "programming",
    title: "Programming Fundamentals",
    description: "Logic, variables, control structures",
    chapters: [
      "Introduction to Programming",
      "Variables & Data Types",
      "Control Structures",
      "Functions",
      "Arrays & Strings",
      "Problem Solving Practice",
    ],
  },
  {
    id: "networks",
    title: "Data Communication & Networking",
    description: "Networks, protocols, OSI model",
    chapters: [
      "Basics of Networking",
      "Transmission Media",
      "Network Devices",
      "OSI Model",
      "TCP/IP",
      "Network Security Basics",
    ],
  },
  // add remaining topics here (total 9)
];

export default function Database() {
  return (
    <section className="py-10 max-w-4xl mx-auto">
      {/* Page header */}
      <header className="mb-10 text-center">
        <h1 className="text-3xl font-bold">A/L ICT Roadmap</h1>
        <p className="text-gray-600 mt-2">
          Follow the syllabus topics and select a chapter to study
        </p>
      </header>

      {/* Roadmap */}
      <div className="relative border-l-2 border-gray-200 pl-6 space-y-12">
        {topics.map((topic, topicIndex) => (
          <div key={topic.id} className="relative">
            {/* Topic marker */}
            <div className="absolute -left-[13px] top-1 w-6 h-6 rounded-full bg-blue-600 border-4 border-white" />

            {/* Topic card */}
            <div className="bg-white rounded-xl border shadow-sm p-5">
              <h2 className="text-xl font-semibold">
                {topicIndex + 1}. {topic.title}
              </h2>
              <p className="text-sm text-gray-500 mt-1">
                {topic.description}
              </p>

              {/* Chapters */}
              <ul className="mt-4 space-y-2">
                {topic.chapters.map((chapter, chapterIndex) => (
                  <li key={chapter}>
                    <Link
                      href={`/study-materials/al-ict/${topic.id}/chapter-${chapterIndex + 1}`}
                      className="block rounded-lg border px-4 py-3 hover:bg-gray-50 transition"
                    >
                      <span className="text-sm text-gray-500">
                        Chapter {chapterIndex + 1}
                      </span>
                      <p className="font-medium">{chapter}</p>
                    </Link>
                  </li>
                ))}
              </ul>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
}
